.plan-card {
  background-color: $white;
  border-radius: 10px;
  padding: 24px 24px 21px;
}

.plan-card__title {
  font-weight: bold;
  line-height: 1;
  color: $twilight-blue;
  margin-top: 0;
  margin-bottom: 20px;
}

.plan-card__number {
  font-size: 16px;
  font-weight: 500;
  line-height: 1;
  letter-spacing: 1.6px;
}

.plan-card__details {
  font-size: 14px;
  font-weight: 500;
  line-height: 1;
  letter-spacing: 1.4px;
  color: $twilight-blue;
  text-transform: uppercase;
  margin: 0;

  &--teammates {
    margin-bottom: 6px;
  }

  &--dates {
    margin-bottom: 20px;
  }
}
